<template>
	<div class="infoPage">
		<VueShowdown :markdown="text"></VueShowdown>
	</div>
</template>
<script>
import VueShowdown from 'vue-showdown';

export default {
	name: 'InfoPage',
	// components: [VueShowdown],
	components: VueShowdown,
	data()
	{
		return {
			text: ''
		};
	},
	mounted()
	{
		this.text = this.ligue?.Desc;
	},
	computed: {
		ligue ()
		{
			return this.$store.getters.ligue;
		}
	},
	watch: {
		ligue()
		{
			this.text = this.ligue.Desc;
		}
	}
};
</script>

