<template>
	<div>
		<div class="bottom-navigation">
			<router-link to="/market" class="bottom-nav-link" :class="{activeLink: currentRoute === 'MarketCards'}">
				<span @click="goToPath('goToMinting')">Minting!</span>
			</router-link>
			<router-link to="/usercards" class="bottom-nav-link" :class="{activeLink: currentRoute === 'UserCards'}">
				<span @click="goToPath('goToNFTs')">NFTs</span>
			</router-link>
			<!-- <router-link to="/results" class="bottom-nav-link" :class="{activeLink: currentRoute === 'ResultsPage'}">
				<span>Result</span>
			</router-link> -->
			<router-link to="/info" class="bottom-nav-link" :class="{activeLink: currentRoute === 'InfoPage'}">
				<span @click="goToPath('goToInfo')">Info</span>
			</router-link>
		</div>
	</div>
</template>

<script>
export default {
	name: 'BottomNavigation',
	data()
	{
		return {
			currentRoute: '',
		};
	},
	mounted()
	{
		this.currentRoute = this.$route.name;
	},
	methods: {
		goToPath(path)
		{
			this.$store.dispatch('sendEvent', {
				event_type: path,
				where: 'bottomNavigation'
			});
		}
	},
	computed:
	{
		user ()
		{
			return this.$store.getters.user;
		}
	},
	watch:
	{
		$route (to)
		{
			this.currentRoute = to.name;
		}
	}
};
</script>

<style lang="scss">
	@import './style.scss';
</style>
