export default {
	actions: {
		sendEvent({ state }, params)
		{
			// console.log('window.Telegram.WebApp.initDataUnsafe.user.id is: ' + window.Telegram.WebApp.initDataUnsafe.user.id);
			fetch('https://api2.amplitude.com/2/httpapi', {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					Accept: '*/*'
				},
				body: JSON.stringify({
					api_key: '2e674e4e427eedb68ff3076bfe34162c',
					events: [{
						user_id: this.state.ton.user.wallet ? this.state.ton.user.wallet : window.Telegram.WebApp.initDataUnsafe.user.id,
						// user_id: this.state.ton.user.wallet ? this.state.ton.user.wallet : 'Anon777',
						event_type: params.event_type,
						user_properties: {
							where: params.where,
							id: params.id
						},
					}]
				})
			})
				.then(response => response.json())
				.then(data => console.log(data))
				.catch(error => console.error(error));
		}
	}
};
