import { baseApi } from '@/api/BaseApi';
export default {
	state: () => ({
		tonLink: '',
		wallet: null,
		showSombols: '',
		user: null
	}),
	mutations: {
		getLink(state, tonLink)
		{
			state.tonLink = tonLink;
		},
		setWallet(state, wallet)
		{
			state.wallet = wallet;
		},
		changeWallet(state, wallet)
		{
			state.wallet = wallet;
			let step = 0;
			for (let i = (wallet.length - 1); i >= 0; i--)
			{
				step++;
				if (step === 6)
					break;
				state.showSombols += wallet[i];
			};
			// console.log('ssss: ', state.showSombols);
		},
		setUser(state, user)
		{
			state.user = user;
			// console.log('setUser: ' + user);
		}
	},
	actions: {
		async GET_TON_LINK({ commit })
		{
			try
			{
				const response = await baseApi.getLink();
				const json = await response.json();
				commit('getLink', json.response);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async CHANGE_WALLET({ commit })
		{
			try
			{
				const response = await baseApi.changeWallet();
				const json = await response.json();
				// console.log('store-wallets: ', json.response.wallet);
				commit('changeWallet', json.response.wallet);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async GET_USER_DATA_FROMBACK({ commit, dispath })
		{
			try
			{
				const response = await baseApi.getUserFromBack();
				const json = await response.json();
				commit('setUser', json.response);
				dispath('GET_NFT_NARKET_LIST', { club: 0, league: 0, pos: '' }); // TODO: need to optimize
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async GET_ACCESS()
		{
			try
			{
				const response = await baseApi.getAccess();
				const json = await response.json();
				console.log(json.response);
			}
			catch (e)
			{
				// console.log('err: ', e);
			};
		},
		async SEND_CONNECT_INFO({ commit }, data)
		{
			try
			{
				// console.log('data: ', JSON.stringify(data));
				const response = await baseApi.tonConnectSdk(data);
				const json = await response.json();
				console.log('=_= SEND_CONNECT_INFO json: ', json);
				// commit('getLink', json.response);
			}
			catch (e)
			{
				// console.log('errTEst: ', e);
			};
		},
		async disconnectWallet({ dispatch })
		{
			try
			{
				await baseApi.disconnectWallet();
				dispatch('GET_USER_DATA_FROMBACK');
				// commit('setUser', { wallet: null });
			}
			catch (e)
			{
				// console.log('errTEst: ', e);
			};
		},
		async SET_WALLET({ commit }, wallet)
		{
			commit('setWallet', wallet);
		},
	},
	getters: {
		getTonLink(state)
		{
			return state.tonLink;
		},
		wallet(state)
		{
			return state.wallet;
		},
		user(state)
		{
			return state.user;
		}
	}
};
